import React from "react";
import { Link } from "react-router-dom";

import "./index.css";

export default function ParentsBlogCard(props) {
  const { id, name, description, img, isFirst } = props.cardContent;
  return (
    <>
      {isFirst && <hr />}
      <Link
        to={`/parentsblog/${id}`}
        className="parents-blog-card flex-row align-start justify-start"
      >
        <img src={img} alt={name} />
        <div>
          <h2>{name}</h2>
          <p>{description.substring(0,250) + "..."}</p>
        </div>
      </Link>
    </>
  );
}
