import React, { useState, useEffect } from "react";
import API from "../../API";
import QuizCard from "../../components/quiz-card";
import quizBg from "../../images/bg.png";
import Loader from "react-loader-spinner";
import BlogBanner from "../../components/blog-banner";
import "./index.css";

export default function Quiz() {
  const [quizList, setQuizList] = useState([]);

  useEffect(() => {
    window.scrollTo(0, 0);

    const fetchData = async () => {
      const result = await API.getAllTests();
      setQuizList(result.data.test);
    };
    fetchData();
    // eslint-disable-next-line
  }, []);

  return (
    <>
      <BlogBanner
        blogBannerInfo={{
          header: "Квизы и тесты",
          backImg: quizBg,
          backColor: "#F7C510",
        }}
      ></BlogBanner>
      <section>
        <div className="quiz-grid base-container base-padding grid-2">
          {quizList.length ? (
            quizList.map((item, id) => (
              <QuizCard
                key={id}
                cardContent={{
                  status: item.status,
                  id: item._id,
                  name: item.title,
                }}
              />
            ))
          ) : (
            <Loader
              type="TailSpin"
              color="#00BFFF"
              height={50}
              width={50}
              style={{ margin: "auto" }}
              timeout={5000}
            />
          )}
        </div>
      </section>
    </>
  );
}
