import React from "react";
import { Switch, Route } from "react-router-dom";
import CoachTests from "../coach-section-tests";
import CoachQuizContainer from "../coach-section-quiz";
import CoachMaterials from "../coach-section-materials";
import CoachMaterialArticle from "../coach-section-material-article";
import CoachManual from "../coach-section-manual";
import CoachAccount from "../coach-section-account";
import CoachNavPanel from "../../components/coach-nav-panel";
import "./index.css";

export default function CoachSection() {
  return (
    <>
      <CoachNavPanel />
      <Switch>
        <Route exact path="/coach" component={CoachMaterials} />
        <Route path="/coach/account" component={CoachAccount} />
        <Route path="/coach/manual" component={CoachManual} />
        <Route path="/coach/tests" exact component={CoachTests} />
        <Route path="/coach/tests/:testId" exact component={CoachQuizContainer} />
        <Route path="/coach/:materialId" component={CoachMaterialArticle} />
      </Switch>
    </>
  );
}
