import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import "./index.css";

export default function CoachAuthorization() {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <section className="coach-section">
      <div className="base-container base-padding flex-col align-center">
        <h1>Добро пожаловать</h1>
        <form className="flex-col align-start">
          <label htmlFor="phone">Введите номер</label>
          <input id="phone" type="phone" placeholder="+996 999 99 99 99"></input>
          <label htmlFor="password">Введите пароль</label>
          <input id="password" type="password" placeholder="********"></input>
          <button>
            <Link to="/coach">Войти</Link>
          </button>
        </form>
      </div>
    </section>
  );
}
