import React, { useState, useEffect } from "react";
import API from "../../API";
import "./index.css";

export default function About() {
  const [aboutList, setAboutList] = useState([]);

  useEffect(() => {
    window.scrollTo(0, 0);
    const fetchData = async () => {
      const result = await API.getAboutUsInfo();
      setAboutList(result.data.aboutUs);
      console.log(result.data.aboutUs);
    };
    fetchData();
    // eslint-disable-next-line
  }, []);

  return (
    <div className="about-section">
      {aboutList.length && (
        <>
          <section className="about-banner">
            <div className="base-container base-padding">
              <h1>{aboutList[0].title}</h1>
              <p>{aboutList[0].content}</p>
            </div>
          </section>
          {aboutList.map((item, id) =>
            id ? (
              <section key={id}>
                <div className="base-container base-padding">
                  <h1>{item.title}</h1>
                  <p>{item.content}</p>
                </div>
              </section>
            ) : (
              <div key={id}></div>
            )
          )}
        </>
      )}
    </div>
  );
}
