import React from "react";
import { NavLink } from "react-router-dom";

import "./index.css";

export default function CoachNavPanel() {
  return (
    <nav className="coach-nav base-container flex-row justify-start">
      <NavLink exact activeClassName="coach-active" to="/coach/tests">
        Пре-пост тесты
      </NavLink>
      <NavLink exact activeClassName="coach-active" to="/coach">
        Материалы
      </NavLink>
      <NavLink exact activeClassName="coach-active" to="/coach/manual">
        Руководство
      </NavLink>
      <NavLink exact activeClassName="coach-active" to="/coach/account">
        Дневник тренера
      </NavLink>
    </nav>
  );
}
