import React from "react";
import { Link } from "react-router-dom";

import "./index.css";

export default function SexBlogSmallCard(props) {
  const { id, name, description, img } = props.cardContent;
  return (
    <Link
      to={`/sexblog/${id}`}
      className="sex-blog-small-card flex-col align-start"
    >
      <img src={img} alt={name} />
      <div>
        <h2>{name}</h2>
        <p>{description.substring(0,120) + "..."}</p>
      </div>
    </Link>
  );
}
