import React, { useState, useEffect } from "react";
import API from "../../API";
import Loader from "react-loader-spinner";
import ParentsBlogCard from "../parents-blog-card";

import "./index.css";

export default function ParentsBlogCardsSection() {
  const [parentBlogArticles, setParentBlogArticles] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      const result = await API.getParentsBlogAllArticles();
      setParentBlogArticles(result.data.blogs);
    };
    fetchData();
    // eslint-disable-next-line
  }, []);

  return (
    <div className="parents-blog-cards-section flex-col align-center">
      {parentBlogArticles.length ? (
        <>
          {parentBlogArticles.map((item, id) => (
            <ParentsBlogCard
              key={id}
              cardContent={{
                id: item._id,
                name: item.title,
                description: item.content,
                img: item.photo,
                isFirst: Boolean(id),
              }}
            ></ParentsBlogCard>
          ))}
        </>
      ) : (
        <Loader
          type="TailSpin"
          color="#00BFFF"
          height={50}
          width={50}
          style={{ margin: "auto" }}
          timeout={5000}
        />
      )}
    </div>
  );
}
