import React from "react";

import "./index.css";

export default class FaqForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      name: "",
      phone: "",
      message: "",
    };
  }

  handleSubmit(e) {}

  resetForm() {
    this.setState({ name: "", phone: "", message: "" });
  }

  render() {
    return (
      <form
        id="contact-form"
        onSubmit={this.handleSubmit.bind(this)}
        method="POST"
        className="faq-form flex-col justify-start align-start"
      >
        <div className="flex-col justify-start align-start">
          <label htmlFor="name">Введите имя</label>
          <input
            type="text"
            placeholder="Иванов Иван"
            name="name"
            id="name"
            value={this.state.name}
            onChange={this.handleUserInput.bind(this)}
          />
        </div>
        <div className="flex-col justify-start align-start">
          <label htmlFor="phone">Введите номер телефона</label>
          <input
            type="text"
            placeholder="+996 999 999 999"
            name="phone"
            id="phone"
            value={this.state.phone_number}
            onChange={this.handleUserInput.bind(this)}
          />
        </div>
        <div className="flex-col justify-start align-start">
          <label htmlFor="message">Задайте вопрос</label>
          <textarea
            type="text"
            name="message"
            placeholder="Хочу узнать что-то, что меня сильно волнует!"
            id="message"
            value={this.state.body}
            onChange={this.handleUserInput.bind(this)}
          />
        </div>
        <button type="submit" className="flex-col justify-center align-center ">
          Отправить
        </button>
      </form>
    );
  }

  handleUserInput(event) {
    this.setState({ [event.target.name]: event.target.value });
    console.log(this.state);
  }
}
