import Axios from "axios";

const http = Axios.create({
    baseURL: "https://sex-demo.neobis.kg/",
    headers: {"Content-Type": "application/json"},
});

export default {
    getParentsBlogAllArticles: () => http.get("article/forParents"),
    getSexBlogAllArticles: () => http.get("article/aboutSex"),
    getAllTags: () => http.get("tag"),
    getAllArticlesByTag: (tagId) => http.get(`tag/${tagId}`),
    getStoryBlogAllArticles: () => http.get("article/history"),
    getArticleByKey: (key) => http.get(`blog/${key}`),
    getAboutUsInfo: () => http.get("aboutUs"),
    getAllTests: () => http.get("test"),
    getAllFaq: () => http.get("faq"),
    getTestByKey: (key) => http.get(`test/${key}`),
};
