import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import FirstArticles from "../../components/first-articles";
import FaqSection from "../../components/faq-section";
import QuizContainer from "../../components/quiz-container";

import main from "../../images/main.jpg";
import "./index.css";

export default function MainPage() {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  const quizInfo = {
    name: "Тест на проверку знаний о сексе",
    questions: [
      {
        question: "Чем больше у мужчин стопа, тем больше у него пенис?",
        alternatives: [
          { text: "Правда", isCorrect: false },
          { text: "Ложь", isCorrect: true },
        ],
        desription:
          "Данный миф широко распространен среди населения. Эта информация не соответствует действительности также, как и мифы про то, что по походке можно определить девственница ли девушка или нет.",
      },
      {
        question: "Где происходит оплодотворение? ",
        alternatives: [
          { text: "Во влагалище", isCorrect: false },
          { text: "В матке", isCorrect: false },
          { text: "В фаллопиевых трубах", isCorrect: true },
          { text: "В яичниках", isCorrect: false },
        ],
        desription:
          "Оплодотворение яйцеклетки сперматозоидом происходит в фаллопиевых или маточных трубах. После оплодотворенная яйцеклетка в течении 72х часов спускается в полость матки, где прикрепляется к эндометрию.",
      },
      {
        question:
          "Беременность может наступить после первого единственного полового акта?",
        alternatives: [
          { text: "Да", isCorrect: true },
          { text: "Нет", isCorrect: false },
        ],
        desription:
          "Зачастую среди населения есть стереотип, что в первый раз забеременеть нельзя. Есть риски забеременеть при каждом незащищенном сексуальном контакте.",
      },
      {
        question: "Кто определяет пол ребенка? ",
        alternatives: [
          { text: "Кайнене (Свекровь)", isCorrect: false },
          { text: "Семейный совет", isCorrect: false },
          { text: "Мужчина", isCorrect: true },
          { text: "Женщина", isCorrect: false },
        ],
        desription:
          "В нашем обществе многие мужчины говорят своим женам, чтобы та родила для них сына. Но вот так сюрприз, потому что пол ребенка определяет мужчина. Так как женщина является носителем XX- хромосом,  а мужчина ХУ. Если Х+У, то пол ребенка будет мужской. Так что теперь смело можете образовать тех, кто будет ругать своих жен.",
      },
      {
        question:
          "Контрацепция — это прерывание беременности на очень ранних сроках?",
        alternatives: [
          { text: "Да", isCorrect: false },
          { text: "Нет", isCorrect: true },
        ],
        desription:
          "В обществе из-за различных убеждений существует стереотип, что контрацепция является методом аборта, но это далеко не правда! Контрацепция- это метод предотвращения нежелательной/незапланированной беременности и предупреждение инфекций, передающих половым путем, в том числе ВИЧ.",
      },
    ],
  };

  return (
    <>
      <section className="main-banner">
        <div className="main-banner-text base-padding flex-row align-center justify-btw">
          <div className="flex-col align-start justify-center">
            <h2>ОБЩЕСТВЕННОЕ ОБЪЕДИНЕНИЕ</h2>
            <h1>АЛЬЯНС ПО РЕПРОДУКТИВНОМУ ЗДОРОВЬЮ</h1>
            <p>
              Просветительская платформа «УятЖок» для подростков и их родителей
              по вопросам нравственно-полового воспитания. Данная платформа
              содержит интересные статьи о репродуктивном здоровье для молодежи,
              советы для родителей от специалистов и интерактивные тесты на
              проверку собственных знаний. Мы хотим, чтобы подростки имели
              доступ к качественной и достоверной информации относительно их
              сексуального репродуктивного здоровья и права.
            </p>
            <Link className="flex-row align-center justify-center" to="/about">
              Подробнее о нас
            </Link>
          </div>
        </div>
        <img src={main} alt="АРЗ" />
      </section>
      <FirstArticles></FirstArticles>
      <div className="main-page-quiz-container">
        <div className="base-container base-padding flex-col justify-center align-center">
          <QuizContainer quizInfo={quizInfo.questions}></QuizContainer>
        </div>
      </div>
      <FaqSection></FaqSection>
    </>
  );
}
