import React, { useEffect } from "react";
import TopBanner from "../../components/top-banner";
import StoryCardsSection from "../../components/story-cards-section";
import StoriesBlogImg from "../../images/storiesBlogImg.png";
import StoriesBlogBg from "../../images/storiesBlogBg.png";

export default function Stories() {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const topBannerInfo = {
    header: "Истории",
    description:
      "Et quis deserunt ipsum culpa sint Lorem excepteur aliquip ex est ullamco. Do Lorem sit sunt et nisi ullamco est officia nulla ullamco pariatur do nisi cillum.",
    frontImg: StoriesBlogImg,
    backImg: StoriesBlogBg,
    backColor: "#75BF43",
  };

  return (
    <>
      <TopBanner topBannerInfo={topBannerInfo}></TopBanner>
      <section>
        <div className="base-container ">
          <StoryCardsSection></StoryCardsSection>
        </div>
      </section>
    </>
  );
}
