import React, { useEffect } from "react";

import BlogBanner from "../../components/blog-banner";
import FaqAnswered from "../../components/faq-answered";
import FaqForm from "../../components/faq-form";

import quizBg from "../../images/bg.png";
import "./index.css";

export default function Faq() {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <section>
      <BlogBanner
        blogBannerInfo={{
          header: "Вопросы и ответы",
          backImg: quizBg,
          backColor: "#32BEBF",
        }}
      ></BlogBanner>

      <div className="faq-page-container base-container base-padding flex-row justify-btw align-start">
        <FaqAnswered></FaqAnswered>
        <FaqForm></FaqForm>
      </div>
    </section>
  );
}
