import React, { useState } from "react";
import { NavLink, Link } from "react-router-dom";

import logo from "../../images/logo.png";

import "./index.css";

export default function Header() {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <header
      style={{
        height: window.innerWidth > 900 ? "auto" : isOpen ? "450px" : "80px",
        transition: "all 0.2s ease-in-out",
      }}
    >
      <div className="base-container flex-row align-center justify-btw">
        <div className="logo-burger flex-row align-center justify-btw">
          <Link to="/">
            <img alt="АРЗ" src={logo} />
          </Link>
          <div className="burger-container" onClick={() => setIsOpen(!isOpen)}>
            <div
              className="burger"
              style={{
                transform: isOpen ? "rotate(45deg)" : "none",
                boxShadow: isOpen
                  ? "none"
                  : "0 6px 0 #1f1e16, 0 -6px 0 #1f1e16",
              }}
            >
              <div
                className="x-line"
                style={{
                  display: isOpen ? "block" : "none",
                }}
              />
            </div>
          </div>
        </div>
        <div
          className="header-content flex-row justify-btw align-center"
          style={{
            clipPath:
              window.innerWidth > 900
                ? "polygon(0 0, 100% 0, 100% 100%, 0 100%)"
                : isOpen
                ? "polygon(0 0, 100% 0, 100% 100%, 0 100%)"
                : "polygon(0 0, 100% 0, 100% 0, 0 0)",
          }}
        >
          <nav className="flex-row justify-btw">
            <NavLink activeClassName="active-about" to="/about">
              О нас
            </NavLink>
            <NavLink activeClassName="active-sexblog" to="/sexblog">
              О сексе и не только
            </NavLink>
            <NavLink activeClassName="active-quiz" to="/quiz">
              Проверь себя
            </NavLink>
            <NavLink activeClassName="active-parentsblog" to="/parentsblog">
              Для родителей
            </NavLink>
            <NavLink activeClassName="active-stories" to="/stories">
              Истории
            </NavLink>
            <NavLink activeClassName="active-faq" to="/faq">
              Вопросы
            </NavLink>
            <NavLink activeClassName="active-coach" to="/coachlogin">
              Для тренеров
            </NavLink>
          </nav>
          <div className="flex-row loclization">
            <div>RU</div>
            <div className="slash">|</div>
            <div>KG</div>
          </div>
          <button>Поддержать</button>
        </div>
      </div>
    </header>
  );
}
