import React, {useEffect} from "react";
import TopBanner from "../../components/top-banner";
import SexBlogCardSection from "../../components/sex-blog-cards-section";

import SexBlogImg from "../../images/sexBlogImg.png";
import SexBlogBg from "../../images/sexBlogBg.png";

import "./index.css";

export default function SexBlog() {
    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);

    const topBannerInfo = {
        header: "О здоровье, сексе и не только",
        description:
            "Et quis deserunt ipsum culpa sint Lorem excepteur aliquip ex est ullamco. Do Lorem sit sunt et nisi ullamco est officia nulla ullamco pariatur do nisi cillum.",
        frontImg: SexBlogImg,
        backImg: SexBlogBg,
        backColor: "#63308F",
    };

    return (
        <>
            <TopBanner topBannerInfo={topBannerInfo}/>
            <section className="sex-blog-section base-padding">
                <div className="base-container">
                    <SexBlogCardSection/>
                </div>
            </section>
        </>
    );
}
