import React from "react";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faInstagram, faFacebookF } from "@fortawesome/free-brands-svg-icons";
import { faEnvelope, faPhoneVolume } from "@fortawesome/free-solid-svg-icons";

import footerLogo from "../../images/footerLogo.png";

import "./index.css";

export default function Footer() {
  return (
    <footer>
      <div className="footer-conatiner base-container flex-row align-start justify-btw">
        <img src={footerLogo} alt="АРЗ" />
        <div className="footer-name">
          <h2>АЛЬЯНС ПО РЕПРОДУКТИВНОМУ ЗДОРОВЬЮ</h2>
          <p>
            Общественное Объединение "Альянс по репродуктивному здоровью"
            является частью Международной Федерации Планирования Семьи
          </p>
        </div>
        <div>
          <h3>Полезные ссылки</h3>
          <div className="flex-col footer-links">
            <Link to="/sexblog">О сексе и не только</Link>
            <Link to="/quiz">Квизы</Link>
            <Link to="/parentsblog">Для родителей</Link>
            <Link to="/stories">Истории</Link>
            <Link to="/faq">Вопросы</Link>
          </div>
        </div>
        <div className="footer-social-container flex-col align-center">
          <h3>Мы в соц сетях</h3>
          <div className="footer-social">
            <a href="google.com" target="_blank" className="footer-icon">
              <FontAwesomeIcon icon={faFacebookF} />
            </a>
            <a href="google.com" target="_blank" className="footer-icon">
              <FontAwesomeIcon icon={faInstagram} />
            </a>
            <a href="google.com" target="_blank" className="footer-icon footer-phone">
              <FontAwesomeIcon icon={faPhoneVolume} />
            </a>
            <a href="google.com" target="_blank" className="footer-icon">
              <FontAwesomeIcon icon={faEnvelope} />
            </a>
          </div>
        </div>
      </div>
    </footer>
  );
}
