import React from "react";
import "./index.css";

export default function TopBanner(props) {
  const {
    header,
    description,
    frontImg,
    backImg,
    backColor,
  } = props.topBannerInfo;

  return (
    <section
      style={{
        backgroundColor: `${backColor}`,
        backgroundImage: `url(${backImg})`,
      }}
      className="top-banner"
    >
      <div className="base-container flex-row align-center justify-btw">
        <div>
          <h1>{header}</h1>
          <p>{description}</p>
        </div>
        <img src={frontImg} alt="" />
      </div>
    </section>
  );
}
