import React from "react";
import { Link } from "react-router-dom";

import "./index.css";

export default function SexBlogBigCard(props) {
  const { id, name, description, img } = props.cardContent;
  return (
    <Link
      to={`/sexblog/${id}`} className="sex-blog-big-card flex-row align-start">
      <img src={img} alt={name} />
      <div>
        <h2>{name}</h2>
        <p>{description.substring(0,250) + "..."}</p>
      </div>
    </Link>
  );
}
