import React from "react";
import { Link } from "react-router-dom";

import "./index.css";

export default function QuizCard(props) {
  const { status, id, name } = props.cardContent;
  const cardColors = ["#F7C510", "#75BF43", "#32BEBF", "#63308F", "#F16822"];
  return (
    <Link
      to={status ? `/quiz/${id}` : `/coach/tests/${id}`}
      className="quiz-card flex-row align-center"
    >
      <div
        className="quiz-card-icon"
        style={{
          backgroundColor: `${
            cardColors[parseInt(Math.floor(Math.random() * 5))]
          }`,
        }}
      />
      <h2>{name}</h2>
    </Link>
  );
}
