import React from "react";
import "./index.css";

export default function BlogBanner(props) {
  const { header, backImg, backColor } = props.blogBannerInfo;

  return (
    <section
      style={{
        backgroundColor: `${backColor}`,
        backgroundImage: `url(${backImg})`,
      }}
      className="blog-banner base-padding"
    >
      <div className="base-container flex-row align-center justify-center">
        <h1>{header}</h1>
      </div>
    </section>
  );
}
