import React, { useState, useEffect } from "react";
import API from "../../API";
import quizBg from "../../images/bg.png";
import QuizContainer from "../../components/quiz-container";

import BlogBanner from "../../components/blog-banner";

export default function QuizPage(props) {
  const [quizInfo, setQuizInfo] = useState({});

  useEffect(() => {
    window.scrollTo(0, 0);
    const fetchData = async () => {
      const result = await API.getTestByKey(props.match.params.quizId);
      setQuizInfo(result.data.test);
    };
    fetchData();
    // eslint-disable-next-line
  }, []);

  return (
    <>
      <BlogBanner
        blogBannerInfo={{
          header: quizInfo.title,
          backImg: quizBg,
          backColor: "#F7C510",
        }}
      />
      <div className="base-container base-padding flex-col justify-center align-center">
        {quizInfo.length || (
          <QuizContainer
            className="main-page-quiz-container"
            quizInfo={quizInfo.questions}
          ></QuizContainer>
        )}
      </div>
    </>
  );
}
