import React, { useEffect } from "react";
import TopBanner from "../../components/top-banner";
import ParentsBlogCardsSection from "../../components/parents-blog-cards-section";

import ParentsBlogImg from "../../images/parentsBlogImg.png";
import ParentsBlogBg from "../../images/parentsBlogBg.png";

import "./index.css";

export default function ParentsBlog() {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const topBannerInfo = {
    header: "Для Родителей",
    description:
      "Et quis deserunt ipsum culpa sint Lorem excepteur aliquip ex est ullamco. Do Lorem sit sunt et nisi ullamco est officia nulla ullamco pariatur do nisi cillum.",
    frontImg: ParentsBlogImg,
    backImg: ParentsBlogBg,
    backColor: "#F16822",
  };

  return (
    <>
      <TopBanner topBannerInfo={topBannerInfo}></TopBanner>
      <section>
        <div className="parents-blog-cards base-container">
          <ParentsBlogCardsSection></ParentsBlogCardsSection>
        </div>
      </section>
    </>
  );
}
