import React from "react";
import {Link} from "react-router-dom"
import faqMan from "../../images/faq-man.png"

import "./index.css";

export default function FaqSection() {
  return (
    <section className="faq-section">
      <div className="base-container flex-row align-center justify-btw">
        <div className="flex-col">
          <h1>У тебя есть вопрос?</h1>
          <h1>Мы рады на него ответить!</h1>
          <Link to="/faq" className="flex-row align-center justify-center">Задать вопрос</Link>
        </div>
        <img src={faqMan} alt="faq" />
      </div>
    </section>
  );
}
