import React from "react";
import { Link } from "react-router-dom";

import "./index.css";

export default function CoachMaterialCard(props) {
  const { id, name, description, img } = props.cardContent;
  return (
    <Link
      to={`/coach/${id}`}
      className="coach-material-card flex-row align-start justify-btw"
    >
      <img src={img} alt={name} />
      <div>
        <h2>{name}</h2>
        <p>{description}</p>
      </div>
    </Link>
  );
}
